import React from 'react';
import './ModelSelectorStyles.css';

export interface ModelOption {
  id: string;
  name: string;
  description: string;
  icon: string;
  category: string;
}

interface ModelSelectorProps {
  onModelSelect: (modelId: string) => void;
  darkMode?: boolean;
}

const ModelSelector: React.FC<ModelSelectorProps> = ({ onModelSelect, darkMode = false }) => {
  const availableModels: ModelOption[] = [
    {
      id: 'water-content',
      name: 'Teor de Umidade do Solo',
      description: 'Relatrio para determinao do teor de umidade do solo conforme ABNT NBR 6457/2016',
      icon: '',
      category: 'Solos'
    },
    {
      id: 'header-only',
      name: 'Cabealho Simples',
      description: 'Modelo bsico apenas com cabealho personalizado',
      icon: '',
      category: 'Bsico'
    },
    {
      id: 'granulometry',
      name: 'Granulometria',
      description: 'Anlise granulomtrica de solos conforme ABNT NBR 7181/2016',
      icon: '',
      category: 'Solos'
    },
    {
      id: 'relatorio-sequencial',
      name: 'Relatrio Sequencial',
      description: 'Formulrio para gerao sequencial de mltiplos relatrios de laboratrio',
      icon: '',
      category: 'Laboratrio'
    },
    {
      id: 'compression',
      name: 'Compresso Simples',
      description: 'Ensaio de resistncia  compresso simples (em desenvolvimento)',
      icon: '',
      category: 'Resistncia'
    }
  ];

  const groupedModels = availableModels.reduce((acc, model) => {
    if (!acc[model.category]) {
      acc[model.category] = [];
    }
    acc[model.category].push(model);
    return acc;
  }, {} as Record<string, ModelOption[]>);

  return (
    <div className={`model-selector ${darkMode ? 'dark' : ''}`}>
      <div className="model-selector-header">
        <h1>Selecione o Modelo de Relatrio</h1>
        <p>Escolha o tipo de relatrio PDF que deseja gerar</p>
      </div>

      <div className="model-categories">
        {Object.entries(groupedModels).map(([category, models]) => (
          <div key={category} className="model-category">
            <h2 className="category-title">{category}</h2>
            <div className="model-grid">
              {models.map((model) => (
                <div
                  key={model.id}
                  className={`model-card ${model.id === 'compression' || model.id === 'granulometry' ? 'disabled' : ''}`}
                  onClick={() => {
                    if (model.id !== 'compression' && model.id !== 'granulometry') {
                      onModelSelect(model.id);
                    }
                  }}
                >
                  <div className="model-icon">{model.icon}</div>
                  <div className="model-info">
                    <h3 className="model-name">{model.name}</h3>
                    <p className="model-description">{model.description}</p>
                    {(model.id === 'compression' || model.id === 'granulometry') && (
                      <span className="coming-soon">Em Breve</span>
                    )}
                  </div>
                </div>
              ))}
            </div>
          </div>
        ))}
      </div>

      <div className="model-selector-footer">
        <p> Dica: Use Ctrl+P para imprimir aps selecionar um modelo</p>
      </div>
    </div>
  );
};

export default ModelSelector;